/* eslint-disable */
import React from 'react';
import withStyles from "@material-ui/core/styles/withStyles";
import Loading from "../../components/Loading";
import dashboardStyle from "../../assets/jss/layouts/dashboardStyle";
import Cubic from "../../components/Cubic";
import s from '../../assets/css/layouts/dashboard.module.css';
import _ from 'lodash';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
class Dashboard extends React.Component{
	state={
			initialloading:true,
			deg:0
		}

		componentDidMount(){
			setTimeout(()=>{
			this.setState({
					initialloading:false
				});
			},2000);

			document.addEventListener('keydown',this.handleKeyPress);
			//changebg();
			window.addEventListener('wheel',_.debounce(this.handleScroll,50), true);
			//window.addEventListener('touchstart',_.debounce(this.handleScrolliPhone,50), true);
			//window.addEventListener('scroll',_.debounce(this.handleScroll,50), true);
		}

		/*componentWillUnmount=()=>{
		    window.removeEventListener("scroll", this.handleScroll);
		  }*/

		 /* handleScrolliPhone = (e) => {

		  		var swipe = e.originalEvent.touches;
    			var start = swipe[0].pageY;
    			this.addEventListener('touchmove',()=>{
    				var contact = e.originalEvent.touches;
			        let end = contact[0].pageY;
			        let distance = end-start;

			        if (distance < -30) // up
			        	this.handlecssup(window.event);
			        if (distance > 30) // down
			        	this.handlecssdown(window.event);
    			}, true);
		  	
		  }*/



		handleScroll = (e) => {

			e.deltaY>=0 && this.handlecssdown(window.event);
			e.deltaY<0 && this.handlecssup(window.event);

			
			
			/*console.log("delta",e.deltaY);
			var limit = window.scrollMaxY || (document.documentElement.scrollHeight - document.documentElement.clientHeight);
			var lastScroll= window.scrollY;
		    this.setState({
					    		lastScrollY:lastScroll,
					    		disable_scroll:true
					    	},function(){
					    		e.deltaY>=0 && this.handlecssdown(window.event);
					    		e.deltaY<0 && this.handlecssup(window.event);
					    		
					    	});*/
		    

		  }



		  handleKeyPress=()=>{
		  	switch (window.event.keyCode) {
		        case 37:
		         console.log("left"); 
		         break;
		        case 38:
		         this.handlecssup(window.event);
		         break;
		        case 39:
				 console.log("right");
		         break;
		        case 40:
			  	 this.handlecssdown(window.event);
	         	 break;
	         	default:
	         		this.handlecssup(window.event);
		         	break;
		        
		    }
		  }


		handlecssdown=e=>{
			//e.preventDefault();
			this.setState({
				deg:this.state.deg+90
			},function(){
				let cube=document.getElementById("cube");
				
				cube.style.transform='translate3d(0,0,-50vh) rotateX('+this.state.deg+'deg)';
				cube.style.transition="all 1s cubic-bezier(0.175,0.885,0.320,1.275)";

			});
		}

		handlecssup=e=>{
			//e.preventDefault();
			this.setState({
				deg:this.state.deg-90
			},function(){
				let cube=document.getElementById("cube");
				cube.style.transform='translate3d(0,0,-50vh) rotateX('+this.state.deg+'deg)';
				cube.style.transition="all 1s cubic-bezier(0.175,0.885,0.320,1.275)";

			});
		}

	render(){
			const {classes,...rest}=this.props;
			const {initialloading,deg}=this.state;
			return(
				<div className={classes.body}>
						{initialloading && <Loading/>}
						{!initialloading &&
							<div>
								
								<Cubic/>
								{!((deg%360)==0)&&<div className={classes.icondivup} onClick={this.handlecssup}>
								  		<div className={s.arrowup}>
										                <span style={{borderTop:'5px solid #FFF',
										                			  borderLeft: '5px solid #FFF'}}>
										                </span>
										                <span style={{borderTop:'5px solid #06A8FF',
										                			  borderLeft: '5px solid #06A8FF'}}>
										                </span>
										                <span style={{borderTop:'5px solid #000',
										                			  borderLeft: '5px solid #000'}}>
										                </span>
										</div>
								</div>
								}

								<div className={classes.icondivdown} onClick={this.handlecssdown}>
								  		<div className={s.arrowdown}>
										                <span style={{borderBottom:'5px solid #FFF',
										                			  borderRight: '5px solid #FFF'}}>
										                </span>
										                <span style={{borderBottom:'5px solid #06A8FF',
										                			  borderRight: '5px solid #06A8FF'}}>
										                </span>
										                <span style={{borderBottom:'5px solid #000',
										                			  borderRight: '5px solid #000'}}>
										                </span>
										  
										</div>
								</div>
								
							</div>
						}

						<div id="aboutbody" 
							 style={{
								position:"fixed",
								height:"20px",
								color:"#000",
								bottom:"20px",
								right:"0px",
								fontFamily:"Montserrat,helvetica neue,Helvetica,Arial,sans-serif",
								display:'flex',
							    alignItems:'center',
							    background:'white',
							    padding:'5px'
							}}>
							scroll or <ArrowUpwardIcon/> or <ArrowDownwardIcon/>
						</div>
				</div>
				);
	}
}



export default withStyles(dashboardStyle)(Dashboard);

